.Banner {
  background-image: url("http://d0wnl0ader.herokuapp.com/694005707/4196/bheeshmaparvam1_2022215.jpg?hash=AgADxA");
  background-size: cover;
  height: 590px;
  color: white;
}
.main {
  right: 20px;
  padding-top: 140px;
  height: 190px;
  width: 45rem;
  line-height: 1.3;

  font: size 0.5rem;
  max-width: 360px;
}
.name {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.button {
  color: white;
  width: 65px;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 2px;
  border-left: 2rem;
  border-right: 2rem;
  border-top: 0.5rem;
  padding-top: 0.3rem;
  padding-bottom: 0.4rem;
  background-color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  margin-right: 1rem;
}
.button:hover {
  color: black;
  background-color: #e6e6e6;
}
.content {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font: size 0.5rem;
  max-width: 360px;
}
.fade {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  margin-top: 145px;
}
